@import '../../global.scss';

.intro{
    background-color: $background;
    display: flex;

    .desc{
        width: 80%;
        @include mobile{
            width: 90%;
            text-align: center;
        }
    }

    @include mobile{
        flex-direction: column;
        align-items: center;
        
    }

    .left{

        flex: 0.5;

        .imgContainer{
            width: 700px;
            height: 700px;
            background-color: #ff6e40;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
            }
        }

        img{
            height: 90%;
        }
    }
    
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;
                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color: #ff6e40;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;
            @include mobile{
                display: none;    
            }
            
        }

        .icon{
            font-size: 55px;
            color: $mainColor;
            animation: blinkCursor 2s infinite;
        }

        @keyframes blinkCursor{
            100%{
                opacity: 0;
            }
        }
    }
}